const navs = ['首页', '工作台', '工作动态', '资源目录', '我的资源', '整合共享统计', '资料中心', '系统管理', '新工作台']
const path = ['home', 'workTable', 'WorkDynamic', 'resourceDirectory', 'myResources', 'share', 'infoCenter', 'systemManage', 'newWorkTable']

export default {
  url: '/menus/null',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'menuList|9': [
        {
          'name|+1': navs,
          'id': '@id',
          'path|+1': path
        }
      ]
    }
  }
}



