
<template>
  <div class="header-box">
    <div class="header-container">
      <div class="title">
          <h1>块数据指挥调度平台</h1>
          <p>欢迎贵州省公安厅数据专员：XXX</p>
      </div>  
    </div>
    <div class="border"></div>  
    <TopMenu
      :menuList = 'menuList'
     />
  </div>
  
</template>

<script>
 
import TopMenu from './topMenu'

export default {
  components: {
    TopMenu
  },
  props: {
    menuList: Array
  }
}
 
</script>

<style lang="scss" scoped>
.header-box {
  position: relative;
  .header-container {
    width: 1200px;
    margin: 0 auto;
    margin-top: 30px;
    h1 {
      color: #253c7c;
    }
    p {
      color: #ff6c00;
      font-size: 16px;
      line-height: 50px;
    }
  }
  .border {
    height: 1px;
    width: 100%;
    background: #bdc8e8;
    position: absolute;
    bottom: 51px;
  }
}
</style>

  



